@import "./utils";

.user-data-tabs {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background: $light;

	display: flex;
	justify-content: center;
	align-items: center;

	.logo {
		position: fixed;
		top: 65px;
		left: 65px;

		.light_char {
			fill: $main
		}
	}
}

.nav-tabs {
	position: absolute;
	top: 65px;
	left: 50%;
	transform: translateX(-50%);
	width: 992px;
	justify-content: stretch;

	li {
		flex: 1;

		&:nth-child(3),
		&:nth-child(6) {
			flex-grow: 2;
		}
	}

	.title {
		font-family: helveticaBold, sans-serif;
		font-size: 48px;
		line-height: 1;
		color: $main;
		text-align: center;
		margin-bottom: 45px;
		display: block;
	}
}

.nav-tabs {
	border-bottom: none;
}

.nav-item .nav-link {
	font-family: helveticaBold, sans-serif;
	font-size: 20px;
	line-height: 1;
	color: $main;
	opacity: 0.3;
	padding: 19px 0 0 0;
}

.nav-item .nav-link,
.nav-tabs .nav-item.show .nav-link,
.nav-tabs .nav-link.active {
	border: none;
	border-radius: 0;
	brder-top: 5px solid rgba(73, 117, 251, 0.35);
	text-align: center;
}

.nav-tabs .nav-link.active {
	border-radius: 0;
	border-top: 5px solid $accent;
	opacity: 1;
}

.tab-content {
	.form_group {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		width: 500px;
		margin: auto;
	}
}